}
.production__play-button:hover,
.production__play-button:focus-visible {
  outline: none;
  transform: scale(1.2) translate(-40%, -40%);
  transition: all 0.3s ease;
}
.production__play-button:active {
  transform: scale(0.8) translate(-60%, -60%);
