.checkbox-list {
  padding: 35px 0 43px;

  @media (min-width: @tablet-width) {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 58px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 25px;
    justify-content: flex-start;
    column-gap: 18px;
  }
}

.checkbox-list__item {
  @media (min-width: @tablet-width) {
    width: 237px;
  }

  @media (min-width: @desktop-width) {
    width: 244px;
  }
}

.checkbox-list__item:not(:last-child) {
  margin-bottom: 43px;

  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.checkbox-list__control {
  display: block;
  padding-left: 61px;
  position: relative;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    padding-left: 80px;
  }
}

.checkbox-list__input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox-list__control-mark {
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 0;
  width: 38px;
  height: 38px;
  background-color: #ffffff;
  border: 2px solid @grey;
  transform: translateY(-50%);

  @media (min-width: @tablet-width) {
    width: 52px;
    height: 52px;
    left: 0;
    transform: translateY(-46%);
  }

  @media (min-width: @desktop-width) {
    transform: translateY(-55%);
    left: -2px;
  }
}

.checkbox-list__input:checked + .checkbox-list__control-mark::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  width: 24px;
  height: 16px;
  background: url("../img/checkbox-tick.svg") no-repeat center top;
  transform: translate(-50%, -50%);

  @media (min-width: @tablet-width) {
    width: 32px;
    height: 22px;
    background: url("../img/tick-tablet.svg") no-repeat center;
  }
}
